<template>
  <div class="pokedex-container">
    <div class="pokedex">
      <div class="pokedex__header">
        <div class="pokedex__top">
          <div :class="['pokedex__circle', circleColor]"></div>
          <div class="pokedex__lights" @click="limpiarPantalla">
            <div class="pokedex__light pokedex__light--blue" data-toggle="tooltip" title="Borrar pantalla"></div>
            <div class="pokedex__light pokedex__light--yellow" data-toggle="tooltip" title="Borrar pantalla"></div>
            <div class="pokedex__light pokedex__light--green" data-toggle="tooltip" title="Borrar pantalla"></div>
          </div>
        </div>
        <div class="pokedex__screen">
          <h2 class="pokedex__nombre">{{ capitalizarNombre(pokemon?.name) }}</h2>
          <img v-if="pokemon" :src="pokemon.sprites.front_default" :alt="`Imagen de ${pokemon.name}`" class="pokedex__image">
          <div v-else class="pokedex__placeholder"></div>
        </div>
        <div class="pokedex__buttons">
          <button @click="retrocederPokemon" class="pokedex__button pokedex__button--left" data-toggle="tooltip" title="Retroceder Pokémon">
            L
          </button>
          <button @click="avanzarPokemon" class="pokedex__button pokedex__button--right" data-toggle="tooltip" title="Avanzar Pokémon">
            R
          </button>
        </div>
      </div>
      <div class="pokedex__body">
        <div class="pokedex__busqueda">
          <input v-model="filtro" @input="autoCompletar" type="text" class="form-control" placeholder="Buscar Pokémon por nombre, ID o tipo">
          <div v-if="sugerencias.length" class="pokedex__sugerencias">
            <ul>
              <li v-for="sugerencia in sugerencias" :key="sugerencia" @click="seleccionarSugerencia(sugerencia)">
                {{ sugerencia }}
              </li>
            </ul>
          </div>
          <button @click="buscarPokemon" class="btn btn-primary">Buscar</button>
        </div>
        <div class="pokedex__info">
          <div v-if="loading" class="pokedex__loading">Cargando...</div>
          <transition name="fade">
            <div v-if="pokemon" class="pokedex__pokemon">
              <h3 class="pokedex__nombre-pokemon">{{ capitalizarNombre(pokemon.name) }}</h3>
              
              <div class="pokedex__details">
                <div class="pokedex__info-col">
                  <div class="pokedex__info-item">
                    <h3>Altura:</h3>
                    <p>{{ pokemon.height }} dm</p>
                  </div>
                  <div class="pokedex__info-item">
                    <h3>Peso:</h3>
                    <p>{{ pokemon.weight }} hg</p>
                  </div>
                  <div class="pokedex__info-item">
                    <h3>Experiencia base:</h3>
                    <p>{{ pokemon.base_experience }}</p>
                  </div>
                  <div class="pokedex__info-item">
                    <h3>Tipos</h3>
                    <ul class="pokedex__types">
                      <li v-for="tipo in pokemon.types" :key="tipo.type.name" :class="tipo.type.name">
                        {{ tipo.type.name }}
                      </li>
                    </ul>
                  </div>
                </div>
                <div class="pokedex__info-col">
                  <div class="pokedex__info-item">
                    <button @click="toggleStats" class="btn btn-info">
                      {{ mostrarStats ? 'Ocultar Estadísticas' : 'Ver Estadísticas' }}
                    </button>
                    <transition name="fade">
                      <ul v-if="mostrarStats" class="pokedex__stats">
                        <li v-for="stat in pokemon.stats" :key="stat.stat.name">
                          <strong>{{ stat.stat.name }}:</strong> {{ stat.base_stat }}
                        </li>
                      </ul>
                    </transition>
                  </div>
                  <div class="pokedex__info-item">
                    <button @click="toggleMoves" class="btn btn-info">
                      {{ mostrarMoves ? 'Ocultar Movimientos' : 'Ver Movimientos' }}
                    </button>
                    <transition name="fade">
                      <ul v-if="mostrarMoves" class="pokedex__moves">
                        <li v-for="move in pokemon.moves" :key="move.move.name">
                          {{ move.move.name }} (Nivel: {{ move.version_group_details[0]?.level_learned_at }})
                        </li>
                      </ul>
                    </transition>
                  </div>
                  <div class="pokedex__info-item">
                    <h3>Habilidades</h3>
                    <ul class="pokedex__abilities">
                      <li v-for="habilidad in pokemon.abilities" :key="habilidad.ability.name">
                        {{ habilidad.ability.name }} ({{ habilidad.is_hidden ? 'Oculta' : 'Visible' }})
                      </li>
                    </ul>
                  </div>
                  <div class="pokedex__info-item">
                    <button @click="guardarFavorito(pokemon)" class="btn btn-warning">Agregar a Favoritos</button>
                    <button v-if="esFavorito(pokemon.id)" @click="borrarFavorito(pokemon.id)" class="btn btn-danger">Borrar de Favoritos</button>
                  </div>
                </div>
              </div>
            </div>
          </transition>
          <div v-if="error" class="pokedex__error">{{ error }}</div>
        </div>
      </div>
      <div class="pokedex__menus">
        <button @click="toggleHistorial" class="btn btn-secondary btn-menu">Mostrar/Ocultar Historial</button>
        <transition name="fade">
          <div v-if="mostrarHistorial" class="pokedex__historial">
            <h3>Historial de Búsquedas</h3>
            <ul>
              <li v-for="item in historial" :key="item.timestamp" @click="mostrarPokemon(item.id)">
                <img :src="item.sprites.front_default" :alt="`Imagen de ${item.name}`" class="pokedex__historial-imagen">
                <span>{{ item.id }} - {{ item.name }} ({{ item.timestamp }})</span>
                <span class="pokedex__types-list">
                  <span v-for="tipo in item.types" :key="tipo.type.name" :class="tipo.type.name">
                    {{ tipo.type.name }}
                  </span>
                </span>
              </li>
            </ul>
          </div>
        </transition>
        <button @click="toggleFavoritos" class="btn btn-secondary btn-menu">Mostrar/Ocultar Favoritos</button>
        <transition name="fade">
          <div v-if="mostrarFavoritos" class="pokedex__favoritos">
            <h3>Pokémon Favoritos</h3>
            <ul>
              <li v-for="item in favoritos" :key="item.id" @click="mostrarPokemon(item.id)">
                <img :src="item.sprites.front_default" :alt="`Imagen de ${item.name}`" class="pokedex__favoritos-imagen">
                <span>{{ item.id }} - {{ item.name }}</span>
                <span class="pokedex__types-list">
                  <span v-for="tipo in item.types" :key="tipo.type.name" :class="tipo.type.name">
                    {{ tipo.type.name }}
                  </span>
                </span>
                <button @click.stop="borrarFavorito(item.id)" class="btn btn-danger btn-sm">X</button>
              </li>
            </ul>
          </div>
        </transition>
        <button @click="borrarDatosGuardados" class="btn btn-danger btn-menu">Borrar Datos Guardados</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import $ from 'jquery';
import 'bootstrap';

export default {
  name: 'App',
  data() {
    return {
      filtro: '',
      pokemon: null,
      loading: false,
      historial: [],
      favoritos: [],
      mostrarHistorial: false,
      mostrarFavoritos: false,
      mostrarStats: false,
      mostrarMoves: false,
      sugerencias: [],
      error: null,
      currentId: 1,
      todosLosPokemones: [],
      todosLosTipos: [],
      circleColor: 'blue'
    };
  },
  created() {
    this.cargarTodosLosPokemones();
    this.cargarTodosLosTipos();
    this.cargarDatosGuardados();
    setInterval(this.cambiarColorCirculo, 500);
  },
  updated() {
    this.$nextTick(() => {
      $('[data-toggle="tooltip"]').tooltip();
    });
  },
  methods: {
    async cargarTodosLosPokemones() {
      try {
        const response = await axios.get('https://pokeapi.co/api/v2/pokemon?limit=1000');
        this.todosLosPokemones = response.data.results.map(pokemon => pokemon.name);
      } catch (error) {
        console.error('Error al cargar todos los Pokémon:', error);
        this.error = 'Error al cargar todos los Pokémon';
      }
    },
    async cargarTodosLosTipos() {
      try {
        const response = await axios.get('https://pokeapi.co/api/v2/type');
        this.todosLosTipos = response.data.results.map(type => type.name);
      } catch (error) {
        console.error('Error al cargar todos los tipos:', error);
        this.error = 'Error al cargar todos los tipos';
      }
    },
    async buscarPokemon() {
      if (!this.filtro) {
        this.error = 'Por favor ingrese un nombre, ID o tipo de Pokémon';
        return;
      }

      this.loading = true;
      this.pokemon = null;
      this.error = null;

      if (this.todosLosTipos.includes(this.filtro.toLowerCase())) {
        this.buscarPorTipo(this.filtro.toLowerCase());
      } else {
        try {
          const response = await axios.get(`https://pokeapi.co/api/v2/pokemon/${this.filtro.toLowerCase()}`);
          this.pokemon = response.data;
          this.currentId = this.pokemon.id;
          this.error = null;
          this.agregarAlHistorial(this.pokemon);
        } catch (error) {
          console.error('Pokémon no encontrado', error);
          this.error = 'Pokémon no encontrado';
          this.pokemon = null;
        } finally {
          this.loading = false;
        }
      }
    },
    async buscarPorTipo(tipo) {
      try {
        const response = await axios.get(`https://pokeapi.co/api/v2/type/${tipo}`);
        const pokemonList = response.data.pokemon.map(p => p.pokemon.name);
        this.sugerencias = pokemonList;
        this.loading = false;
      } catch (error) {
        console.error('No se encontraron Pokémon de ese tipo', error);
        this.error = 'No se encontraron Pokémon de ese tipo';
        this.loading = false;
      }
    },
    async cargarPokemon(id) {
      this.loading = true;
      this.pokemon = null;
      this.error = null;
      try {
        const response = await axios.get(`https://pokeapi.co/api/v2/pokemon/${id}`);
        this.pokemon = response.data;
        this.currentId = this.pokemon.id;
        this.error = null;
      } catch (error) {
        console.error('Pokémon no encontrado', error);
        this.error = 'Pokémon no encontrado';
        this.pokemon = null;
      } finally {
        this.loading = false;
      }
    },
    avanzarPokemon() {
      const nextId = this.currentId + 1;
      this.cargarPokemon(nextId);
    },
    retrocederPokemon() {
      if (this.currentId > 1) {
        const prevId = this.currentId - 1;
        this.cargarPokemon(prevId);
      }
    },
    agregarAlHistorial(pokemon) {
      const timestamp = new Date().toLocaleString();
      this.historial.push({ ...pokemon, timestamp });
      this.guardarHistorial();
    },
    guardarFavorito(pokemon) {
      if (!this.favoritos.find(item => item.id === pokemon.id)) {
        this.favoritos.push(pokemon);
        this.guardarFavoritos();
      }
    },
    borrarFavorito(id) {
      this.favoritos = this.favoritos.filter(item => item.id !== id);
      this.guardarFavoritos();
    },
    esFavorito(id) {
      return this.favoritos.some(item => item.id === id);
    },
    mostrarPokemon(id) {
      this.cargarPokemon(id);
      this.cerrarMenus();
    },
    toggleHistorial() {
      this.mostrarHistorial = !this.mostrarHistorial;
      if (this.mostrarHistorial) {
        this.cerrarOtrosMenus('mostrarHistorial');
      }
    },
    toggleFavoritos() {
      this.mostrarFavoritos = !this.mostrarFavoritos;
      if (this.mostrarFavoritos) {
        this.cerrarOtrosMenus('mostrarFavoritos');
      }
    },
    toggleStats() {
      this.mostrarStats = !this.mostrarStats;
      if (this.mostrarStats) {
        this.cerrarOtrosMenus('mostrarStats');
      }
    },
    toggleMoves() {
      this.mostrarMoves = !this.mostrarMoves;
      if (this.mostrarMoves) {
        this.cerrarOtrosMenus('mostrarMoves');
      }
    },
    autoCompletar() {
      if (this.filtro.length > 1) {
        const filtroLower = this.filtro.toLowerCase();
        const pokemonSugerencias = this.todosLosPokemones.filter(pokemon =>
          pokemon.toLowerCase().includes(filtroLower)
        ).slice(0, 5);
        const tipoSugerencias = this.todosLosTipos.filter(tipo =>
          tipo.toLowerCase().includes(filtroLower)
        ).slice(0, 5);
        this.sugerencias = [...pokemonSugerencias, ...tipoSugerencias];
      } else {
        this.sugerencias = [];
      }
    },
    seleccionarSugerencia(sugerencia) {
      this.filtro = sugerencia;
      this.sugerencias = [];
      this.buscarPokemon();
    },
    capitalizarNombre(nombre) {
      if (!nombre) return '';
      return nombre.charAt(0).toUpperCase() + nombre.slice(1);
    },
    cambiarColorCirculo() {
      const colors = ['blue', 'green', 'yellow', 'red'];
      const currentColorIndex = colors.indexOf(this.circleColor);
      const nextColorIndex = (currentColorIndex + 1) % colors.length;
      this.circleColor = colors[nextColorIndex];
    },
    limpiarPantalla() {
      this.pokemon = null;
      this.filtro = '';
      this.sugerencias = [];
      this.error = null;
    },
    cerrarMenus() {
      this.mostrarHistorial = false;
      this.mostrarFavoritos = false;
      this.mostrarStats = false;
      this.mostrarMoves = false;
    },
    cerrarOtrosMenus(menuAbierto) {
      if (menuAbierto !== 'mostrarHistorial') {
        this.mostrarHistorial = false;
      }
      if (menuAbierto !== 'mostrarFavoritos') {
        this.mostrarFavoritos = false;
      }
      if (menuAbierto !== 'mostrarStats') {
        this.mostrarStats = false;
      }
      if (menuAbierto !== 'mostrarMoves') {
        this.mostrarMoves = false;
      }
    },
    guardarHistorial() {
      localStorage.setItem('historial', JSON.stringify(this.historial));
    },
    guardarFavoritos() {
      localStorage.setItem('favoritos', JSON.stringify(this.favoritos));
    },
    cargarDatosGuardados() {
      const historialGuardado = localStorage.getItem('historial');
      const favoritosGuardados = localStorage.getItem('favoritos');
      if (historialGuardado) {
        this.historial = JSON.parse(historialGuardado);
      }
      if (favoritosGuardados) {
        this.favoritos = JSON.parse(favoritosGuardados);
      }
    },
    borrarDatosGuardados() {
      localStorage.removeItem('historial');
      localStorage.removeItem('favoritos');
      this.historial = [];
      this.favoritos = [];
    },
    reproducirSonido() {
      if (this.pokemon && this.pokemon.id) {
        const audio = new Audio(`https://raw.githubusercontent.com/PokeAPI/cries/main/cries/${this.pokemon.id}.ogg`);
        audio.play();
      }
    }
  }
};
</script>

